import {
	all,
	call,
	fork,
	take,
	put,
	race,
	delay,
	takeEvery,
	takeLatest,
	actionChannel,
} from 'redux-saga/effects';
import {
	commandLsAction,
	PWD_FAILURE,
	PWD_REQUEST,
	PWD_SUCCESS,
} from '../../reducers/sftp';
import messageSender from './messageSender';
import {messageReader} from './messageReader';
import {closeChannel, subscribe} from '../channel';

function* sendCommand(action) {
	const {payload} = action;
	const channel = yield call(subscribe, payload.socket);
	console.log(' !');
	try {
		yield call(messageSender, {
			keyword: 'CommandByPwd',
			ws: payload.socket,
		});

		while (true) {
			const {timeout, data} = yield race({
				timeout: delay(5000),
				data: take(channel),
			});
			if (timeout) {
				console.log('PWD   . .');
				closeChannel(channel);
			} else {
				const res = yield call(messageReader, {data, payload});
				const past = payload.path;
				const prev = payload.pathList;
				const next = res.pathList;
				const temp = prev.filter((v) => next.includes(v));
				const temp2 = next.filter((v) => ![past].includes(v));
				temp.pop();
				const remove = prev.filter((v) => !next.includes(v));

				const add = next.filter((v) => !prev.includes(v));
				const shouldLs = temp2.filter((v) => !temp.includes(v));
				switch (res.type) {
					case PWD_SUCCESS:
						console.log('');
						console.log([past]);
						console.log('  ');
						console.log(temp);
						console.log('  + 1');
						//   0       .
						//  1     +1  .
						console.log(prev.filter((v) => !next.includes(v)));
						console.log(' ');
						console.log(next.filter((v) => !prev.includes(v)));
						console.log('  ');
						console.log(temp2.filter((v) => !temp.includes(v)));

						// success  filelist initializing...
						// pathList      +    pathlist  filelist 
						yield put({
							type: PWD_SUCCESS,
							payload: {
								uuid: payload.uuid,
								path: res.path,
								pathList: res.pathList,
								removeIndex:
									add.length === 0 // 
										? remove.length === 0 // 
											? 1 //  refresh  1
											: remove.length + 1 //  + 1
										: remove.length === 0
										? 0 //    .
										: remove.length, //     .
							},
						});
						//      
						if (add.length !== 0) {
							for (let value of add) {
								console.log(value);
								yield put(
									commandLsAction({
										...payload,
										newPath: value,
									}),
								);
							}
						} else if (remove.length === 0) {
							// 0  0
							console.log('refresh');
							console.log(past);
							yield put(
								commandLsAction({
									...payload,
									newPath: past,
								}),
							);
						} else {
							for (let value of shouldLs) {
								// 0  
								console.log('remove exist');
								console.log(value);
								yield put(
									commandLsAction({
										...payload,
										newPath: value,
									}),
								);
							}
						}
						break;
				}
			}
		}
	} catch (err) {
		console.log(err);
		yield put({type: PWD_FAILURE});
		alert('PWD  !');
		closeChannel(channel);
	}
}

function* watchSendCommand() {
	yield takeEvery(PWD_REQUEST, sendCommand);

	// const reqChannel = yield actionChannel(PWD_REQUEST);
	// console.log('watch send command pwd');
	// while (true) {
	// 	const action = yield take(reqChannel);
	// 	console.log('pwd request start!!');
	// 	yield call(sendCommand, action);
	// }
}

export default function* commandPwdSaga() {
	yield all([fork(watchSendCommand)]);
}
