import {
	all,
	call,
	fork,
	take,
	put,
	race,
	delay,
	takeLatest,
	actionChannel,
} from 'redux-saga/effects';
import {
	CD_FAILURE,
	CD_REQUEST,
	CD_SUCCESS,
	commandLsAction,
	commandPwdAction,
	INITIAL_FILELIST,
	PWD_SUCCESS,
} from '../../reducers/sftp';
import messageSender from './messageSender';
import {closeChannel, subscribe} from '../channel';
import {messageReader} from './messageReader';
import {OPEN_ALERT_POPUP} from '../../reducers/popup';

function* sendCommand(action) {
	const {payload} = action;
	console.log(payload);
	const channel = yield call(subscribe, payload.socket);
	try {
		yield call(messageSender, {
			keyword: 'CommandByCd',
			ws: payload.socket,
			path: payload.newPath,
		});
		while (true) {
			const {timeout, data} = yield race({
				timeout: delay(200),
				data: take(channel),
			});
			if (timeout) {
				console.log('CD   . .');
				closeChannel(channel);
			} else {
				const res = yield call(messageReader, {data, payload});
				let past = payload.path;
				let prev = payload.pathList;
				let next = [];
				let temp = [];
				let temp2 = [];
				let remove = [];
				let add = [];
				let shouldLs = [];

				switch (res.type) {
					case CD_SUCCESS:
						yield put({
							type: CD_SUCCESS,
							payload: {uuid: payload.uuid},
						});

						yield call(messageSender, {
							keyword: 'CommandByPwd',
							ws: payload.socket,
						});
						break;

					case PWD_SUCCESS:
						past = payload.path;
						prev = payload.pathList;
						next = res.pathList;
						temp = prev.filter((v) => next.includes(v));
						temp2 = next.filter((v) => ![past].includes(v));
						temp.pop();
						remove = prev.filter((v) => !next.includes(v));
						add = next.filter((v) => !prev.includes(v));
						shouldLs = temp2.filter((v) => !temp.includes(v));

						// success  filelist initializing...
						// pathList      +    pathlist  filelist 
						yield put({
							type: PWD_SUCCESS,
							payload: {
								uuid: payload.uuid,
								path: res.path,
								pathList: res.pathList,
								removeIndex:
									add.length === 0 // 
										? remove.length === 0 // 
											? 1 //  refresh  1
											: remove.length + 1 //  + 1
										: remove.length === 0
										? 0 //    .
										: remove.length, //     .
							},
						});
						//      
						if (add.length !== 0) {
							for (let value of add) {
								console.log(value);
								yield put(
									commandLsAction({
										...payload,
										newPath: value,
									}),
								);
							}
						} else if (remove.length === 0) {
							// 0  0
							console.log('refresh');
							console.log(past);
							yield put(
								commandLsAction({
									...payload,
									newPath: past,
								}),
							);
						} else {
							for (let value of shouldLs) {
								// 0  
								console.log('remove exist');
								console.log(value);
								yield put(
									commandLsAction({
										...payload,
										newPath: value,
									}),
								);
							}
						}
						break;
					// yield put(commandPwdAction(payload));
				}
			}
		}
	} catch (err) {
		console.log(err);
		yield put({type: CD_FAILURE});
		yield put({
			type: OPEN_ALERT_POPUP,
			data: 'wrong_path',
		});
		closeChannel(channel);
	}
}

function* watchSendCommand() {
	const reqChannel = yield actionChannel(CD_REQUEST);
	console.log('watch send command connection');
	while (true) {
		const action = yield take(reqChannel);
		console.log('cd request start!!');
		yield call(sendCommand, action);
	}
}

export default function* commandCdSaga() {
	yield all([fork(watchSendCommand)]);
}
